const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AlertDialogSlide(props) {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = (event) => {
    setOpen(true);
    event.preventDefault();
  };

  const handleClose = (event) => {
    setOpen(false);
    // event.preventDefault();
  };

  return (
    <div
      style={{
        position: "absolute",
        bottom: props.bottomPosition,
        left: props.leftPosition,
        transform: "translate(-50%, -50%)"
      }}
    >
      {/* <Button variant="outlined" color="primary" onClick={handleClickOpen}>
        Slide in alert dialog
      </Button> */}

      <IconButton
        style={{
          color: props.buttonColour,
          opacity: props.buttonOpacity
        }}
        variant="outlined"
        color="primary"
        href="#contained-buttons"
        onClick={handleClickOpen}
      >
        <p style={{ fontSize: "0.8rem", paddingRight: "5px" }}>
          {props.leftText}
        </p>
        <RadioButtonCheckedIcon />
        <p style={{ fontSize: "0.8rem", paddingLeft: "5px" }}>
          {props.rightText}
        </p>
      </IconButton>

      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title" style={{ color: "dimgrey", paddingBottom: "0px" }}>
          <strong>
          {"IMXGES"}
          </strong>
        </DialogTitle>

        <DialogTitle id="alert-dialog-slide-title" style={{ color: "grey", paddingBottom: "0px" }}>
          {"You're about to open a new window"}
        </DialogTitle>

        <DialogContent>
          <DialogContentText
            id="alert-dialog-slide-description"
            onClick={handleClose}
          >
            <a
              href={props.link}
              target="_blank"
              rel="noreferrer nofollow nooppener"
              style={{ textDecoration: "none", color: "steelblue" }}
            >
              {props.linkText}
            </a>
          </DialogContentText>

{/* conditional rendering for the image, no prop left a blank box */}
{/* {props.dialogImage != undefined ? console.log("prop used") : console.log("no prop used") } */}

{props.dialogImage != undefined ? <a
              href={props.link}
              target="_blank"
              rel="noreferrer nofollow nooppener"
              style={{ textDecoration: "none", color: "steelblue" }}
            ><img
              src={props.dialogImage}
              alt="desk setup example"
              style={{
                marginLeft: "auto",
                marginRight: "auto",
                width: "100%",
                height: "auto",
                marginBottom:"10px"
              }}
            /></a> : null }
          

          <DialogContentText id="alert-dialog-slide-description">
            {props.dialogText}
          </DialogContentText>

          {/* <DialogContentText id="alert-dialog-slide-description">
            Thanks for using our affiliate link!
          </DialogContentText> */}

        </DialogContent>
        <DialogActions style={{paddingTop:"0px"}}>
          <Button
            onClick={handleClose}
            color="primary"
            style={{ color: "steelblue" }}
          >
            stay here
          </Button>
          <Button onClick={handleClose} color="primary">
            <a
              href={props.link}
              target="_blank"
              rel="noreferrer nofollow nooppener"
              style={{ textDecoration: "none", color: "steelblue" }}
            >
              go ahead
            </a>
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
